<template>
    <el-dialog title="节点信息"
               :visible="pageInfo.isShow"
               :modal-append-to-body="false"
               :close-on-click-modal="false"
               :center="true"
               :lock-scroll="false"
               @close="onClose(module_name,'pageDataRef')"
    >
        <el-form :model="pageData" :rules="pageDataRules" ref="pageDataRef" :inline="true" label-width="160px" size="small">
            <el-form-item label="父节点id" prop="parent_id">
                <el-input disabled v-model="pageData.parent_id"></el-input>
            </el-form-item>
            <el-form-item label="显示名称" prop="label">
                <el-input disabled v-model="pageData.label"></el-input>
            </el-form-item>
            <el-form-item label="路由显示名称" prop="router_show_name">
                <el-input v-model="pageData.router_show_name"></el-input>
            </el-form-item>
            <el-form-item label=" 路由名称" prop="router_name">
                <el-input v-model="pageData.router_name"></el-input>
            </el-form-item>
            <el-form-item label="路由路径" prop="router_path">
                <el-input v-model="pageData.router_path" style="width:696px"></el-input>
            </el-form-item>
            <el-form-item label="排序" prop="order">
                <el-input type="number" step="0.01" v-model="pageData.order"></el-input>
            </el-form-item>

            <el-form-item label="是否有图标" prop="router_has_icon">
                <el-select v-model="pageData.router_has_icon" placeholder="请选择" clearable>
                    <el-option v-for="item in mongodb_bool_type_code_list"
                               :key="item.codeId"
                               :label="item.codeValue"
                               :value="item.codeId">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item v-if="pageData.router_has_icon === 'true'" label="图标样式" prop="router_icon_class">
                <el-input v-model="pageData.router_icon_class"></el-input>
            </el-form-item>
            <el-form-item v-if="pageData.router_has_icon === 'true'" label="图标大小" prop="router_icon_class">
                <el-input v-model="pageData.router_icon_size"></el-input>
            </el-form-item>
            <el-form-item label="备注" prop="remark">
                <el-input type="textarea" v-model="pageData.remark" style="width:696px"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="onClose(module_name,'pageDataRef')">关 闭</el-button>
            <el-button v-if="pageInfo.pageModel === 'add'" type="primary" @click="onSubmit">确 定</el-button>
            <el-button v-else-if="pageInfo.pageModel === 'edit'" type="primary" @click="onUpdate">更 新</el-button>
        </div>
    </el-dialog>
</template>
<script>
    export default {
        name: 'sysRouterInfoManageNodeInfo',
        data() {
            return {
                module_name: 'sysRouterInfoManageNode',
                refresh_page_method: 'refresh_' + this.module_name,
                mongodb_bool_type_code_list: null,
                pageData: {},
                pageDataRules: {
                    router_show_name: [{required: true, message: '请输入路由显示名称', trigger: 'blur'}],
                    router_name: [{required: true, message: '请输入路由名称', trigger: 'blur'}],
                    router_path: [{required: true, message: '请输入路由路径', trigger: 'blur'}],
                }
            }
        },
        watch: {
            'pageData.router_show_name': {
                handler(newName, oldName) {
                    this.pageData.label = newName;
                },
                deep: true,
                immediate: true
            },
        },
        computed: {
            pageInfo() {
                let _page_data = this.$setPageInfo(this.module_name);
                if (_page_data.pageModel === 'add') {
                    let cur_node_info = this.$store.getters.get_cur_node_info;
                    this.pageData = {
                        'parent_id': cur_node_info.id,
                        'router_has_icon': 'false'
                    };

                }else if (_page_data.pageModel === 'view' || _page_data.pageModel === 'edit') {
                    let cur_node_info = this.$store.getters.get_cur_node_info;
                    this.pageData = cur_node_info.value;
                }
                return _page_data;
            }
        },
        async mounted() {
            this.mongodb_bool_type_code_list = await this.$setCodeList(this.mongodb_bool_type_code_list, 'mongodb_bool_type');
        },
        methods: {
            async onSubmit() // 提交按钮
            {
                let _this = this;
                this.$refs.pageDataRef.validate(async (valid) => {
                    if (valid) {
                        let item = this.pageData;
                        item['label'] = item.router_show_name;
                        let res = await _this.$insert(item, 'OAMP_insert_sys_router_info');
                        if (res != null) {
                            _this.$message.success('新增信息成功');
                            _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
                        } else {
                            _this.$message.error('新增信息失败');
                        }
                    }
                });
            },
            async onUpdate() // 更新按钮
            {
                let _this = this;
                this.$refs.pageDataRef.validate(async (valid) => {
                    if (valid) {
                        let item = this.pageData;
                        let res = await _this.$update(item, 'OAMP_update_sys_router_info');
                        if (res != null) {
                            _this.$message.success('修改信息成功');
                            _this.$onClose(_this.module_name, 'pageDataRef', _this.refresh_page_method);
                        } else {
                            _this.$message.error('修改信息失败');
                        }
                    }
                });
            }
        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .el-input {
        width: 260px;
    }

    .el-select {
        width: 260px;
    }

    .el-date-editor.el-input, .el-date-editor.el-input__inner {
        width: 260px;
    }
</style>


