<template>
    <div class="v-container">
        <!-- 面包屑 -->
        <el-breadcrumb>
            <el-breadcrumb-item :to="{ name: 'setting' }">设置</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ name: 'sysManage' }">系统管理</el-breadcrumb-item>
            <el-breadcrumb-item>系统功能信息管理</el-breadcrumb-item>
        </el-breadcrumb>
        <hr>
        <el-row>
            <el-col :span="10">
                <el-col :offset="2" :span="20">
                    <el-input
                            placeholder="输入关键字进行过滤"
                            v-model="filterText">
                    </el-input>
                </el-col>
                <el-col :offset="2" :span="20" style="text-align: center;margin: 10px 30px;">
                    <el-button v-if="is_has_top_node === false" size="small" type="primary" @click="add_sys_router_top_node_info">添加顶级节点</el-button>
                    <el-button v-if="is_selected_node" size="small" type="primary" @click="add_node_info">添加节点</el-button>
                    <el-button v-if="is_selected_node" size="small" type="primary" @click="edit_node_info">修改节点信息</el-button>
                    <el-button v-if="is_selected_node" size="small" type="primary" @click="view_node_info">查看节点信息</el-button>
                    <el-button v-if="is_selected_node" size="small" type="primary" @click="delete_node_info">删除节点</el-button>
                    <el-button size="small" type="primary" @click="refresh_node_info">刷新</el-button>
                </el-col>
                <el-col :offset="3" :span="18">
                    <div style="margin: 0px 0px 5px;">当前选择节点:【{{selected_data.label}}】</div>
                </el-col>
                <el-col :offset="2" :span="20">
                    <div style="height:690px;overflow-y:scroll;background-color: #fff;padding: 10px">
                        <el-tree
                                draggable
                                :data="treeData"
                                :props="defaultProps"
                                default-expand-all
                                :filter-node-method="filterNode"
                                @node-click="node_click"
                                :render-content="renderContent"
                                ref="tree_ref">
                        </el-tree>
                    </div>
                </el-col>
            </el-col>
            <el-col :span="14">
                <el-table :data="tableData" :border="true" :stripe="true" size="mini" :height="getTableHeight(32)">
                    <el-table-column fixed type="index" :index="onIndexMethod()" label="序号" width="60"></el-table-column>
                    <el-table-column
                            prop="createTime"
                            label="创建时间"
                            width="156">
                    </el-table-column>
                    <el-table-column
                            :show-overflow-tooltip="true"
                            prop="router_show_name"
                            label="显示名称"
                            width="110">
                    </el-table-column>
                    <el-table-column
                            :show-overflow-tooltip="true"
                            prop="router_name"
                            label="路由名称"
                            width="110">
                    </el-table-column>
                    <el-table-column
                            :show-overflow-tooltip="true"
                            prop="router_path"
                            label="路由路径">
                    </el-table-column>
                    <el-table-column
                            label="图标"
                            width="50">
                        <template slot-scope="scope">
                            <i :class="'iconfont ' + scope.row.router_icon_class "></i>
                        </template>
                    </el-table-column>
                    <el-table-column
                            prop="router_icon_size"
                            label="尺寸"
                            width="50">
                    </el-table-column>
                    <el-table-column
                            :show-overflow-tooltip="true"
                            prop="router_icon_class"
                            label="图标样式"
                            width="180">
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <!-- 默认隐藏的详情页【对话框形式】-->
        <nodeInfo></nodeInfo>
    </div>
</template>

<script>
    import nodeInfo from './sysFunctionInfoManage/nodeInfo'

    export default {
        name: "sysFunctionInfoManage",
        components: {nodeInfo},
        data() {
            return {
                module_name: 'sysRouterInfoManageNode',
                refresh_page_method: 'refresh_' + this.module_name,
                searchFormData: {
                    order: '+router_path'
                },
                tableData: [],
                is_has_top_node: true,
                is_selected_node: false,
                selected_data: {},
                filterText: '',
                treeData: [],
                defaultProps: {
                    children: 'children',
                    label: 'label'
                }
            }
        },
        created() {
            this.$store.state.Apps.$on(this.refresh_page_method, () => {
                this.refresh_node_info()
            });
        },
        async mounted() {
            this.search();
            this.search_tree_data();
        },
        watch: {
            filterText(val) {
                this.$refs.tree_ref.filter(val);
            }
        },
        methods: {
            async search() {
                if (this.selected_data.hasOwnProperty('value'))
                    this.searchFormData.parent_id = this.selected_data.value.node_id;
                let data = await this.$search(this.searchFormData, 'OAMP_search_sys_router_info_list');
                this.searchFormData.pageCount = data.pageCount;
                this.tableData = data.data;
            },
            async search_tree_data() {
                let data = await this.$exec({}, 'OAMP_search_sys_router_tree_data');
                this.is_has_top_node = data !== null;
                if (data === null) {
                    this.treeData = [];
                } else {
                    this.treeData = [data];
                }
            },
            async add_sys_router_top_node_info()/*添加路由顶级节点*/ {
                let res = await this.$insert({}, 'OAMP_insert_sys_router_top_node_info');
                if (res != null) {
                    this.$message.success('新增顶级节点信息成功');
                    this.is_has_top_node = true;
                    this.search_tree_data();
                } else {
                    this.$message.error('新增顶级节点信息失败');
                }
            },
            renderContent: function (h, {node, data, store}) {
                return h('span', [
                    h('i', {attrs: {class: 'iconfont ' + data.value.router_icon_class}}),
                    h('span', "     "),
                    h('span', node.label),
                ]);
            },
            filterNode(value, data) {
                if (!value) return true;
                return data.label.indexOf(value) !== -1;
            },
            node_click(data, it, item) {
                this.is_selected_node = true;
                this.selected_data = data;
                this.search();
            },
            add_node_info()/*添加节点信息*/ {
                this.onAdd('sysRouterInfoManageNode');
                this.$store.commit('change_cur_node_info', this.selected_data);
            },
            edit_node_info() /*修改节点信息*/ {
                this.onEdit('sysRouterInfoManageNode', 0, [this.selected_data]);
                this.$store.commit('change_cur_node_info', this.selected_data);
            },
            view_node_info() /*查看节点信息*/ {
                this.onView('sysRouterInfoManageNode', 0, [this.selected_data]);
                this.$store.commit('change_cur_node_info', this.selected_data);
            },
            async delete_node_info()/*删除节点信息*/ {
                if (await this.$del(this.selected_data, 'OAMP_delete_sys_router_info')) {
                    this.$message.success('删除路由节点信息成功');
                    this.is_selected_node = false;
                    this.selected_data={};
                    this.search_tree_data();
                }
            },
            refresh_node_info()/*刷新节点信息*/ {
                this.search_tree_data();
                this.search();
            }
        }
    }
</script>

<style scoped>
</style>


